


































import { Component, Prop, Vue } from 'vue-property-decorator'

@Component
export default class PasswordComponent extends Vue {
  @Prop({ required: false })
  value!: string

  @Prop({ required: false })
  rules!: Array<() => boolean>

  @Prop({ default: false })
  label!: boolean

  @Prop({ default: false })
  disabled!: boolean

  @Prop({ default: false })
  readonly!: boolean

  @Prop({ default: '350px' })
  maxWidth!: string

  @Prop({ default: -1 })
  tabindex!: number

  isShow = false

  mounted () {
    if (this.value && this.value !== '') {
      this.isShow = false
    }
  }

  get password () {
    return this.value
  }

  set password (value: string) {
    this.$emit('input', value)
  }
}
